import React from 'react'
import styled from 'styled-components'

import { useDownshift } from '../../atoms/DownshiftProvided'
import { Spacing } from '../../styling'
import { ISuggestion } from './ISuggestion'

interface IProps extends React.InputHTMLAttributes<HTMLInputElement> {}

const StyledAddressSuggestInput = styled.div`
  input {
    width: 100%;
    margin: ${Spacing.MarginAroundFormComponents};
    padding: 0.6rem;
  }
`

export const AddressSuggestInput: React.FC<IProps> = ({ ...inputProps }) => {
  const downshift = useDownshift<ISuggestion>()
  return (
    <StyledAddressSuggestInput>
      <input
        {...inputProps}
        {...downshift.getInputProps({
          onClick: () => {
            downshift.openMenu()
          },
          onFocus: () => {
            downshift.openMenu()
          },
        })}
        data-testid="suggest-input"
      />
    </StyledAddressSuggestInput>
  )
}
