import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components'

import { useDownshift } from '../../atoms/DownshiftProvided'
import { Colors } from '../../styling'
import { AddressSuggestItems } from './AddressSuggestItems'
import { DiscoverSuggestions } from './DiscoverSuggestions'
import { ISuggestion } from './ISuggestion'

interface IProps extends React.InputHTMLAttributes<HTMLInputElement> {
  previousLocations?: ReadonlyArray<ISuggestion>
  minSearchLength?: number
}

const StyledAddressSuggestMenu = styled.div`
  padding: 0;
  margin-top: 0.1rem;
  position: absolute;
  z-index: 10;
  background-color: ${Colors.white};
  width: 100%;
  max-height: 20rem;
  overflow-y: auto;
  overflow-x: hidden;
  outline: 0;
  transition: opacity 0.1s ease;
  border-radius: 0.28571429rem 0.28571429rem;
  box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);

  &.isOpen {
    padding: 0.3rem;
    border-color: hsl(0, 0%, 80%);
    border-width: 0.1rem;
    border-style: solid;
  }

  .hint,
  .item {
    padding: 0.4rem;
  }
  .hint {
    color: ${Colors.greyMiddle};
  }
  .item {
    cursor: pointer;
  }
  .groupLabel {
    color: ${Colors.greyMiddle};
    font-variant-caps: all-petite-caps;
    padding: 0.1rem 0.4rem;
  }
  .item.active {
    background-color: ${Colors.greyLight};
  }
`

export const AddressSuggestMenu: React.FC<IProps> = ({
  minSearchLength,
  previousLocations,
}) => {
  const downshift = useDownshift<ISuggestion>()
  const renderMenu = () => {
    if (previousLocations && previousLocations.length) {
      return (
        <>
          <div className="groupLabel">
            <Trans>Previous addresses</Trans>
          </div>
          <AddressSuggestItems items={previousLocations} />
          <div className="groupLabel">
            <Trans>Matching to input</Trans>
          </div>
          <DiscoverSuggestions
            minLength={minSearchLength}
            initialIndex={previousLocations.length}
          />
        </>
      )
    }
    return <DiscoverSuggestions minLength={minSearchLength} />
  }
  return (
    <div style={{ position: 'relative' }}>
      <StyledAddressSuggestMenu
        {...downshift.getMenuProps({
          className: downshift.isOpen ? 'isOpen' : '',
        })}
        data-testid="suggest-menu"
      >
        {downshift.isOpen ? renderMenu() : null}
      </StyledAddressSuggestMenu>
    </div>
  )
}
