import { Trans } from '@lingui/macro'
import React from 'react'

import { useDownshift } from '../../atoms/DownshiftProvided'
import { useQAddressSuggest } from '../../graph/generated'
import { AddressSuggestItems } from './AddressSuggestItems'

interface IProps {
  minLength?: number
  debounceInterval?: number
  initialIndex?: number
}

export const DiscoverSuggestions: React.FC<IProps> = ({
  minLength = 3,
  debounceInterval = 300,
  initialIndex,
}) => {
  const { inputValue } = useDownshift()
  const [search, setSearch] = React.useState<string>(inputValue || '')

  React.useEffect(
    () => {
      function debouncedSearch() {
        setSearch(inputValue || '')
      }
      const timer = setTimeout(debouncedSearch, debounceInterval)
      return () => clearTimeout(timer)
    },
    [inputValue],
  )

  const skip = search.length < minLength
  const { data, loading } = useQAddressSuggest(
    {
      search,
    },
    { skip, fetchPolicy: 'cache-first' },
  )

  if (skip) {
    return (
      <div className="hint">
        <Trans>Start typing to search for an address</Trans>
      </div>
    )
  }

  if (loading) {
    return (
      <div className="hint">
        <Trans>Searching...</Trans>
      </div>
    )
  }

  return (
    <AddressSuggestItems items={data.suggest} initialIndex={initialIndex} />
  )
}
