import React from 'react'
import styled from 'styled-components'

import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { OrderMenuSidedishRemoveButton } from './OrderMenuSidedishRemoveButton'

interface IProps {
  label: string
  onAdd(): void
  onRemove(): void
  count: number
}

const StyledOrderMenuSidedishButton = styled.div`
  margin: 0 1rem 1rem 0;
  width: 8rem;
  display: inline-block;
  position: relative;

  & .count {
    display: inline-block;
    margin-left: 0.5rem;
  }
`

export const OrderMenuSidedishButton: React.FC<IProps> = ({
  label,
  count,
  onAdd,
  onRemove,
}) => (
  <StyledOrderMenuSidedishButton>
    <TouchButton
      onClick={onAdd}
      color={count > 0 ? Colors.orange : Colors.greyDark}
    >
      <span>{label}</span>
      <span key={count} className="animated heartBeat count">
        {count > 1 && `${count}x`}
      </span>
    </TouchButton>
    {count > 0 && <OrderMenuSidedishRemoveButton onClick={() => onRemove()} />}
  </StyledOrderMenuSidedishButton>
)
