import React from 'react'
import styled from 'styled-components'

import { Flex } from '../../../atoms/Flex'
import { SizerButton } from '../../../atoms/SizerButton'
import { FCreateOrderItems } from '../../../graph/generated'
import { TouchButton } from '../../../molecules/TouchButton'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'
import { Colors, Spacing } from '../../../styling'
import { OrderCartLine } from '../atoms/OrderCartLine'

interface IProps {
  item: FCreateOrderItems
  onRemove(): void
  onEdit(): void
}

const OrderCartItemStyled = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  border-top: 1px solid ${Colors.greyMiddle};
  padding: 0.1rem 0;
`

const TouchButtonColumn = styled.div`
  flex: 0 0 auto;
  padding-right: ${Spacing.Small};
  width: ${Spacing.ButtonColumnWidthPx};
`

export const OrderCartItem: React.FC<IProps> = ({ item, onRemove, onEdit }) => (
  <OrderCartItemStyled>
    <TouchButtonColumn>
      <TouchButtonDelete onDelete={onRemove} />
    </TouchButtonColumn>
    <TouchButtonColumn>
      <SizerButton>
        <TouchButton
          onClick={onEdit}
          iconName="mode_edit"
          disabled={item.recipe.sidedishCount === 0}
        />
      </SizerButton>
    </TouchButtonColumn>

    <Flex>
      <OrderCartLine
        key={item.id}
        code={item.recipe.code}
        label={item.recipe.nameLabel}
        price={item.price.value}
      />
      {item.sidedishes.map(sidedish => (
        <OrderCartLine
          key={sidedish.id}
          code={`+${sidedish.recipe.code}`}
          label={sidedish.recipe.nameLabel}
          price={sidedish.price.value}
        />
      ))}
    </Flex>
  </OrderCartItemStyled>
)
