import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React, { useRef } from 'react'
import styled from 'styled-components'

import { Loading } from '../../../atoms/Loading'
import { MarginBox } from '../../../atoms/MarginBox'
import { useRoot } from '../../../atoms/RootProvider'
import { VerticalGap } from '../../../atoms/VerticalGap'
import { useQOrderCart } from '../../../graph/generated'
import { useOrderCreateControl } from '../hooks/useOrderCreateControl'
import { OrderCartItem } from '../molecules/OrderCartItem'
import { OrderCartTotals } from '../molecules/OrderCartTotals'

const CartStyled = styled.div`
  margin: 0.1rem 0.1rem 0.1rem -2rem;
  position: relative;
`
const MinimalOrderPrice = styled.div`
  margin: 0 0 0.5rem 0;
  text-align: right;
`

const EmptyCart = ({ children }) => (
  <MarginBox margin="0.5rem 0 0 0.5rem">
    <Trans>Cart is empty</Trans>
    {children}
  </MarginBox>
)

export const OrderCart = observer(() => {
  const { createOrder } = useRoot()
  const { onItemEdit, onItemRemove } = useOrderCreateControl()

  const previousCart = useRef<ReactNode | undefined>(undefined)
  const { data, loading } = useQOrderCart(
    {
      orderId: createOrder.orderId!,
    },
    { skip: !createOrder.hasOrder },
  )

  let minimalPrice: undefined | string | ReactNode = createOrder.minimalPrice
  if (minimalPrice) {
    minimalPrice = (
      <MinimalOrderPrice>
        <Trans>
          Minimal order price is <b>{minimalPrice}</b>
        </Trans>
      </MinimalOrderPrice>
    )
  }

  if (!createOrder.hasOrder) {
    return <EmptyCart>{minimalPrice}</EmptyCart>
  }

  if (loading) {
    return previousCart.current || <Loading />
  }

  const { order } = data

  if (order.countWares === 0) {
    return <EmptyCart>{minimalPrice}</EmptyCart>
  }

  previousCart.current = (
    <CartStyled>
      {minimalPrice}
      <OrderCartTotals order={order} />
      <VerticalGap />
      {order.items.map(item => (
        <OrderCartItem
          key={item.id}
          item={item}
          onEdit={() => onItemEdit(item.id)}
          onRemove={() => onItemRemove(item.id)}
        />
      ))}
    </CartStyled>
  )
  return previousCart.current
})
