import { Tooltip } from '@material-ui/core'
import { Attachment } from '@material-ui/icons'
import { SECOND } from '@speedlo/hooks'
import React from 'react'
import styled from 'styled-components'

import { Card } from '../../../atoms/Card'
import { Price } from '../../../atoms/Price'
import { FOrderMenuRecipeFragment } from '../../../graph/generated'
import { useLongPress } from '../../../hooks/useLongPress'
import { Colors } from '../../../styling'

interface IProps {
  recipe: FOrderMenuRecipeFragment
  onUse(withSidedish: boolean): void
}

const RecipeStyled = styled(Card)`
  box-sizing: border-box;
  height: 5.8rem;
  cursor: pointer;
  padding: 0.5rem 0 0 0.3rem;
  margin: 0 0.5rem 0.5rem 0;
  &:hover {
    background-color: ${Colors.yellow};
  }
  user-select: none !important;
`

const RecipeName = styled.div`
  padding-top: 0.2rem;
`

const RecipeCode = styled.span`
  font-weight: bold;
  border: 1px solid ${Colors.greyMiddle};
  border-radius: 0.5rem 0 0 0;
  padding: 0.1rem;
`

const RecipePrice = styled(Price)`
  position: absolute;
  right: 1.1rem;
  top: 4.5rem;
  font-weight: 500;
  font-size: 1.1rem;
  color: ${Colors.greyDarkest};
  text-align: right;
  overflow: hidden;
  white-space: nowrap;
`

const RecipeHasSidedish = styled(Attachment)`
  position: absolute;
  right: 1rem;
  top: 0.2rem;
` as typeof Attachment

export const OrderMenuRecipeCard: React.FC<IProps> = ({ recipe, onUse }) => {
  const longPress = useLongPress(() => {
    onUse(true)
  })
  return (
    <Tooltip title={recipe.descriptionLabel} enterDelay={SECOND}>
      <RecipeStyled
        onClick={ev => {
          if (!ev.defaultPrevented) {
            onUse(false)
          }
        }}
        onContextMenu={ev => {
          ev.preventDefault()
        }}
        {...longPress}
      >
        <RecipeCode>{recipe.code}</RecipeCode>
        <RecipeName>{recipe.nameLabel}</RecipeName>
        <RecipePrice
          price={recipe.price.value}
          currencyCode={recipe.price.currency.code}
        />
        {recipe.sidedishCount > 0 ? (
          <RecipeHasSidedish
            onClickCapture={ev => {
              ev.preventDefault()
              onUse(true)
            }}
          />
        ) : null}
      </RecipeStyled>
    </Tooltip>
  )
}
