import { debounce } from 'debounce'
import { reaction } from 'mobx'
import { useDisposable } from 'mobx-react-lite'

import { useRoot } from '../../../atoms/RootProvider'
import { useMCreateOrderUpdate } from '../../../graph/generated'

export function useUpdateOrder() {
  const { createOrder } = useRoot()
  const [updateOrder] = useMCreateOrderUpdate()
  const debouncedUpdate = debounce(updateOrder, 100)

  // update order whenever these would change
  useDisposable(() =>
    reaction(
      () => createOrder.updateOrderVariables,
      variables => {
        if (variables.orderId) {
          debouncedUpdate(variables)
        }
      },
    ),
  )
}
