import { useApolloClient } from '@speedlo/graphql'
import { reaction } from 'mobx'
import { useDisposable } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  FCreateOrderRecipeSidedishesFragment,
  QCreateOrderItemDocument,
  QCreateOrderItemQuery,
  QCreateOrderItemVariables,
  QCreateOrderRecipeDocument,
  QCreateOrderRecipeQuery,
  QCreateOrderRecipeVariables,
} from '../../../graph/generated'

interface IState {
  recipe?: FCreateOrderRecipeSidedishesFragment
  selection: ID[]
}

const resetState = (): IState => ({
  recipe: undefined,
  selection: [],
})

export function useOrderSidedishControl() {
  const { createOrder } = useRoot()
  const client = useApolloClient()
  const [state, setState] = React.useState<IState>(resetState)

  const selectForRecipe = async (recipeId: ID) => {
    const result = await client.query<
      QCreateOrderRecipeQuery,
      QCreateOrderRecipeVariables
    >({
      query: QCreateOrderRecipeDocument,
      variables: {
        recipeId,
      },
    })
    setState({ recipe: result.data.recipe, selection: [] })
  }

  const selectForItem = async (itemId: ID) => {
    const result = await client.query<
      QCreateOrderItemQuery,
      QCreateOrderItemVariables
    >({
      query: QCreateOrderItemDocument,
      variables: { itemId },
    })
    const { item } = result.data
    setState({
      recipe: item.recipe,
      selection: item.selectedSidedishes.map(sel => sel.recipe.id),
    })
  }

  useDisposable(() =>
    reaction(
      () => createOrder.sidedishMode,
      mode => {
        if (mode === 'none') {
          setState(resetState)
        }
        if (mode === 'recipe' && createOrder.sidedishRootId) {
          selectForRecipe(createOrder.sidedishRootId)
        }
        if (mode === 'item' && createOrder.sidedishRootId) {
          selectForItem(createOrder.sidedishRootId)
        }
      },
    ),
  )

  const sidedishCount = sidedishId => {
    return state.selection.filter(id => id === sidedishId).length
  }

  const onSidedishAdd = sidedishId => {
    let { selection } = state
    selection = selection.concat(sidedishId)
    setState(prev => ({ ...prev, selection }))
  }

  const onSidedishRemove = sidedishId => {
    const { selection } = state
    const index = selection.indexOf(sidedishId)
    if (index > -1) {
      selection.splice(index, 1)
    }
    setState(prev => ({ ...prev, selection }))
  }

  const onConfirm = () => {
    createOrder.sidedishSelectionConfirmed(state.selection)
    setState(resetState)
  }

  const onCancel = () => {
    createOrder.sidedishSelectionCancel()
    setState(resetState)
  }

  return {
    selectForRecipe,
    selectForItem,
    isOpen: Boolean(state.recipe),
    recipe: state.recipe,
    sidedishCount,
    onSidedishAdd,
    onSidedishRemove,
    onConfirm,
    onCancel,
  }
}
