import { Trans } from '@lingui/macro'
import { DF, RM } from '@speedlo/tools'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { CardListTitle } from '../../../atoms/CardListTitle'
import { Loading } from '../../../atoms/Loading'
import {
  OrderStateCategoryEnum,
  QOrderListOrders,
  QOrderListQuery,
  SOrderListSubscription,
  useQOrderList,
  useSOrderList,
} from '../../../graph/generated'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { OrderNotificationSound } from '../atoms/OrderNotificationSound'
import { useOrderListCategories } from '../hooks/useOrderListCategories'
import { useOrderQueryControl } from '../hooks/useOrderQueryControl'
import { OrderListCategory } from '../molecules/OrderListCategory'
import { TCategorizedOrders } from '../order.types'

export const OrderList = observer(() => {
  const {
    categories,
    categorizeOrders,
    getCategoryLabel,
  } = useOrderListCategories()

  const [
    getOrders,
    { queryCategories, branchesFilter, noFilter, skipQuery },
  ] = useOrderQueryControl<TCategorizedOrders>(categories)

  const { data, loading, refetch, updateQuery } = useQOrderList(
    { branches: branchesFilter, categories: queryCategories },
    { skip: skipQuery },
  )

  useOnConnectionRestored(refetch)

  useSOrderList(
    { branches: branchesFilter, categories },
    {
      skip: skipQuery,
      onSubscriptionData({ subscriptionData }) {
        if (subscriptionData.data) {
          updateQuery(prev => onOrderListUpdate(subscriptionData.data!, prev))
        }
      },
    },
  )

  if (noFilter) {
    return (
      <CardListTitle>
        <Trans>Select at least one branch</Trans>
      </CardListTitle>
    )
  }

  const orders = getOrders(loading, () => {
    return categorizeOrders(skipQuery ? [] : data.orders)
  })

  if (orders === null) {
    return <Loading />
  }

  return (
    <>
      {categories.map(category => (
        <OrderListCategory
          key={category}
          category={category}
          categoryLabel={getCategoryLabel(category)}
          orders={orders[category] || []}
          showBranches={branchesFilter.length > 1}
        />
      ))}
      <OrderNotificationSound orders={orders} />
    </>
  )
})

function onOrderListUpdate(
  update: SOrderListSubscription,
  prev: QOrderListQuery,
) {
  const { order } = update.updatedOrder
  if (!order) {
    return prev
  }
  let orderExists = false
  const orders = RM.reject(isOldFinishedOrder, prev.orders).map(prevOrder => {
    if (prevOrder.id === order.id) {
      orderExists = true
      return order
    }
    return prevOrder
  })
  if (!orderExists) {
    orders.unshift(order)
  }
  return { ...prev, orders }
}

function isOldFinishedOrder(order: QOrderListOrders) {
  return (
    order.orderStateCategory === OrderStateCategoryEnum.Finished &&
    DF.differenceInHours(new Date(), order.createdAt) >
      order.companyBranch.oldOrderThreshold
  )
}
