import { Trans } from '@lingui/macro'
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@material-ui/core'
import React from 'react'
import styled from 'styled-components'

import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { OrderMenuSidedishButton } from '../atoms/OrderMenuSidedishButton'
import { useOrderSidedishControl } from '../hooks/useOrderSidedishControl'

const SidedishCategory = styled.div`
  font-size: 1rem;
  font-weight: bold;
  text-align: left;
  margin: 0 0 0.2rem 0.2rem;
  color: ${Colors.black};
`

export const OrderSidedishModal: React.FC = () => {
  const {
    isOpen,
    recipe,
    onCancel,
    onConfirm,
    onSidedishAdd,
    onSidedishRemove,
    sidedishCount,
  } = useOrderSidedishControl()

  const renderLoading = () => (
    <DialogTitle>
      <Trans>Loading sidedishes...</Trans>
    </DialogTitle>
  )

  const renderRecipe = () => (
    <>
      <DialogTitle>
        <Trans>{`Add something else to ${recipe!.nameLabel}?`}</Trans>
      </DialogTitle>
      <DialogContent>
        <div>
          {recipe!.sidedishCategories.map(category => (
            <div key={category.id}>
              <SidedishCategory>{category.nameLabel}</SidedishCategory>
              {category.sidedishes.map(sidedish => (
                <span key={`${sidedish.id}-${category.id}`}>
                  <OrderMenuSidedishButton
                    label={sidedish.nameLabel}
                    onAdd={() => onSidedishAdd(sidedish.id)}
                    onRemove={() => onSidedishRemove(sidedish.id)}
                    count={sidedishCount(sidedish.id)}
                  />
                </span>
              ))}
            </div>
          ))}
        </div>
      </DialogContent>
      <DialogActions>
        <TouchButton text={i18n.t`Cancel`} onClick={onCancel} />
        <TouchButton
          text={i18n.t`Confirm`}
          onClick={onConfirm}
          color={Colors.green}
        />
      </DialogActions>
    </>
  )

  return (
    <Dialog open={isOpen} onClose={onCancel} scroll="paper" maxWidth="lg">
      {recipe ? renderRecipe() : renderLoading()}
    </Dialog>
  )
}
