import React from 'react'
import { AutoSizer, Grid } from 'react-virtualized'

import { Flex } from '../../../atoms/Flex'
import { FOrderMenuRecipeFragment } from '../../../graph/generated'
import { OrderMenuRecipeCard } from '../atoms/OrderMenuRecipeCard'

interface IProps {
  recipes: ReadonlyArray<FOrderMenuRecipeFragment>
  onRecipeUse(recipe: FOrderMenuRecipeFragment, withSidedishes: boolean): void
  columnCount?: number
}

export const OrderMenuRecipes: React.FC<IProps> = ({
  recipes,
  onRecipeUse,
  columnCount = 5,
}) => {
  const recipesCount = recipes.length
  const rowCount = Math.ceil(recipesCount / columnCount)

  return (
    <Flex>
      <AutoSizer>
        {({ width, height }) => (
          <Grid
            rowCount={rowCount}
            columnCount={columnCount}
            columnWidth={width / columnCount - 4}
            width={width}
            height={height}
            rowHeight={100}
            cellRenderer={({ columnIndex, rowIndex, key, style }) => {
              const recipeIndex = rowIndex * columnCount + columnIndex
              if (recipeIndex >= recipesCount) {
                return null
              }
              const recipe = recipes[recipeIndex]
              return (
                <div style={{ ...style }} key={key}>
                  <OrderMenuRecipeCard
                    recipe={recipe}
                    onUse={(withSidedish: boolean) =>
                      onRecipeUse(recipe, withSidedish)
                    }
                  />
                </div>
              )
            }}
          />
        )}
      </AutoSizer>
    </Flex>
  )
}
