import React from 'react'

const isTouchDevice = (() => {
  try {
    document.createEvent('TouchEvent')
    return true
  } catch (e) {
    return false
  }
})()

export function useLongPress(onLongPress: () => void) {
  const shouldShortPress = React.useRef(true)
  const moved = React.useRef(false)
  const timer = React.useRef(0)

  if (!isTouchDevice) {
    return {}
  }

  const onTouchStart = (ev: React.TouchEvent<HTMLElement>) => {
    shouldShortPress.current = true
    moved.current = false
    timer.current = window.setTimeout(() => {
      shouldShortPress.current = false
      if (moved.current === false) {
        onLongPress()
      }
    }, 1000)
  }

  const onTouchMove = (ev: React.TouchEvent<HTMLElement>) => {
    moved.current = true
  }

  const onTouchEnd = (ev: React.TouchEvent<HTMLElement>) => {
    if (timer.current) {
      window.clearTimeout(timer.current)
    }
  }

  return { onTouchStart, onTouchMove, onTouchEnd }
}
