import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { useAcceptOrder } from '../hooks/useAcceptOrder'
import { useUpdateOrder } from '../hooks/useUpdateOrder'
import { ICall } from '../models/CreateOrderModel'

interface IProps {
  call?: ICall
}

export const OrderFormSetup: React.FC<IProps> = ({ call, children }) => {
  const { createOrder } = useRoot()

  React.useEffect(() => {
    // reset model on mount, eventually it should be recreated here from scratch instead
    createOrder.resetModel()
  }, [])

  React.useEffect(
    () => {
      createOrder.userIsCalling(call)
    },
    [call],
  )

  useUpdateOrder()

  const { onSubmit, onSubmitFailure } = useAcceptOrder()

  return useObserver(() => (
    <Form
      values={createOrder.formValues}
      onSubmit={onSubmit}
      onSubmitFailure={onSubmitFailure}
      onChange={formState =>
        createOrder.updateFormValues(formState.values, formState.touched)
      }
    >
      {children}
    </Form>
  ))
}
