import React from 'react'
import { Field, FieldType } from 'react-form'
import styled from 'styled-components'

import { Radiuses, Spacing } from '../styling'
import { fullWidthStyle } from './FormInputStyled'

interface IStyleProps {
  fullWidth?: boolean
}

interface IProps extends IStyleProps, React.TextareaHTMLAttributes<never> {
  field: FieldType
}

const FormInputTextBoxMultilineStyled = styled.textarea`
  padding: 0.5rem;
  border-radius: ${Radiuses.TextComponent};
  resize: none;
  overflow: auto;
  box-sizing: border-box;
  margin: ${Spacing.MarginAroundFormComponents};
  ${({ fullWidth }: IStyleProps) => fullWidth && fullWidthStyle};
`

export const FormInputTextBoxMultiline: React.FC<IProps> = ({
  field,
  children,
  fullWidth = false,
  ...props
}) => (
  <Field field={field}>
    {fieldApi => (
      <FormInputTextBoxMultilineStyled
        {...props}
        value={fieldApi.value || ''}
        onChange={e => {
          fieldApi.setValue(e.target.value)
        }}
        onBlur={() => fieldApi.setTouched(true)}
        fullWidth={fullWidth}
      />
    )}
  </Field>
)
