import { useObserver } from 'mobx-react-lite'
import React from 'react'
import Goodbye from 'react-goodbye'

import { ConfirmDialog } from '../../../atoms/ConfirmDialog'
import { useRoot } from '../../../atoms/RootProvider'
import { useMCancelOrder } from '../../../graph/generated'
import { i18n } from '../../../i18n'

export const OrderCancelPrompt: React.FC = () => {
  const { createOrder } = useRoot()
  const [doCancelOrder] = useMCancelOrder()

  const cancelOrder = async () => {
    if (createOrder.orderId) {
      await doCancelOrder({ orderId: createOrder.orderId })
    }
  }

  return useObserver(() => (
    <Goodbye when={createOrder.hasOrder}>
      {({ isShow, handleOk, handleCancel }) => (
        <ConfirmDialog
          isShow={isShow}
          confirmText={i18n.t`Do you really want to cancel order?`}
          onConfirm={() => {
            cancelOrder()
            handleOk()
          }}
          onCancel={handleCancel}
        />
      )}
    </Goodbye>
  ))
}
