import { useRouterContext } from '@speedlo/hooks'
import _ from 'lodash'
import { ValidationResultGroup } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { useMCreateOrderAccept } from '../../../graph/generated'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'

export function useAcceptOrder() {
  const { createOrder } = useRoot()
  const { enqueueSuccess, enqueueWarning } = useSnackBar()
  const { history } = useRouterContext()

  const [acceptOrder] = useMCreateOrderAccept()

  const onSubmit = async () => {
    const data = await acceptOrder(createOrder.acceptOrderVariables)
    if (data) {
      if (data.finishOrder.userErrors.length > 0) {
        data.finishOrder.userErrors.forEach(enqueueWarning)
        return
      }
      createOrder.resetOrder()
      history.replace('/order')
      enqueueSuccess(i18n.t`Order has been accepted`)
    }
  }

  const onSubmitFailure = (errors: ValidationResultGroup<unknown>) => {
    const stringErrors = _.values<string>(errors)
    stringErrors.forEach(enqueueWarning)
  }

  return { onSubmit, onSubmitFailure }
}
