import React from 'react'
import styled from 'styled-components'

import { applyAlphaToColor, Colors } from '../../../styling'

interface IProps {
  onClick(): void
}

const applyAlpha = (alphaValue: number) => () =>
  applyAlphaToColor(Colors.orange, alphaValue)

const StyledOrderMenuSidedishRemoveButton = styled.div`
  position: absolute;
  left: -1rem;
  top: -1rem;
  width: 2rem;
  height: 2rem;
  border-radius: 1rem;
  background-color: ${applyAlpha(1)};

  &:hover:enabled {
    background-color: ${applyAlpha(0.9)};
  }
  &:active:enabled {
    background-color: ${applyAlpha(1)};
  }
  & .rectangle {
    width: 45%;
    position: absolute;
    top: 45%;
    left: 30%;
    height: 4px;
    background: white;
  }
`
export const OrderMenuSidedishRemoveButton: React.FC<IProps> = ({
  onClick,
  ...props
}) => {
  return (
    <StyledOrderMenuSidedishRemoveButton
      onClick={onClick}
      className="animated bounceIn"
      {...props}
    >
      <div className="rectangle" />
    </StyledOrderMenuSidedishRemoveButton>
  )
}
