import { i18nMark } from '@lingui/core'
import { Trans } from '@lingui/macro'
import { useNow } from '@speedlo/hooks'
import { addMinutes } from 'date-fns'
import { Observer, observer, useObserver } from 'mobx-react-lite'
import React from 'react'
import { Field, FieldType } from 'react-form'
import styled from 'styled-components'

import { getMinutesForDeliveryType } from '../../../atoms/DeliveryMinutes'
import { FormInputDateTime } from '../../../atoms/FormInputDateTime'
import { useRoot } from '../../../atoms/RootProvider'
import { SelectControlFlat } from '../../../atoms/SelectControlFlat'
import {
  QOrderFormDeliverAtDefaultsTimes,
  useQOrderFormDeliverAtDefaults,
} from '../../../graph/generated'
import { i18n } from '../../../i18n'

interface IProps {
  field: FieldType
}

enum EDeliverAtType {
  ASAP,
  AT_TIME,
  IN_MINUTES,
}

export const labels = {
  [EDeliverAtType.ASAP]: i18nMark('ASAP'),
  [EDeliverAtType.AT_TIME]: i18nMark('At time'),
  [EDeliverAtType.IN_MINUTES]: i18nMark('In minutes'),
}

interface ITypeProps extends IProps {
  times: QOrderFormDeliverAtDefaultsTimes
}

const OrderFormDeliverAtContent = styled.div`
  padding: 0.5rem;
`

const OrderFormDeliverAsap: React.FC<ITypeProps> = ({ times }) => {
  const { createOrder } = useRoot()
  const { deliveryTime, pickupTime } = times
  return useObserver(() => {
    if (createOrder.isDelivery) {
      return (
        <Trans>
          Current estimated delivery time is <b>{deliveryTime} minutes</b>
        </Trans>
      )
    } else {
      return (
        <Trans>
          Current estimated pickup time is <b>{pickupTime} minutes</b>
        </Trans>
      )
    }
  })
}

const OrderFormDeliverAtTime: React.FC<ITypeProps> = ({ field }) => {
  return <FormInputDateTime field={field} openTo="hours" disablePast={true} />
}

const OrderFormDeliverInMinutes: React.FC<ITypeProps> = ({ field, times }) => {
  const getNow = useNow()
  const { createOrder } = useRoot()
  const [minutes, setMinutes] = React.useState(() => {
    const time = createOrder.isDelivery ? times.deliveryTime : times.pickupTime
    if (createOrder.deliveryMinutes !== undefined) {
      return createOrder.deliveryMinutes
    }
    return time
  })
  return (
    <Field field={field}>
      {fieldApi => (
        <Observer>
          {() => (
            <SelectControlFlat<number>
              items={getMinutesForDeliveryType(createOrder.deliveryType)}
              selectedItem={minutes}
              onSingleSelection={(min: number) => {
                setMinutes(min)
                fieldApi.setValue(addMinutes(getNow(), min))
              }}
              formatItem={min => min.toString()}
            />
          )}
        </Observer>
      )}
    </Field>
  )
}

function getFieldForType(type: EDeliverAtType) {
  switch (type) {
    case EDeliverAtType.AT_TIME:
      return OrderFormDeliverAtTime
    case EDeliverAtType.IN_MINUTES:
      return OrderFormDeliverInMinutes
    default:
      return OrderFormDeliverAsap
  }
}

export const OrderFormDeliverAt = observer<IProps>(({ field }) => {
  const { createOrder } = useRoot()
  const [currentType, setType] = React.useState(EDeliverAtType.ASAP)
  const { data, loading } = useQOrderFormDeliverAtDefaults({
    branchId: createOrder.currentBranch.id,
  })
  return (
    <>
      <SelectControlFlat<EDeliverAtType>
        items={[
          EDeliverAtType.ASAP,
          EDeliverAtType.AT_TIME,
          EDeliverAtType.IN_MINUTES,
        ]}
        selectedItem={currentType}
        onSingleSelection={type => setType(type || EDeliverAtType.ASAP)}
        formatItem={type => i18n._(labels[type])}
      />
      <OrderFormDeliverAtContent>
        {!loading &&
          React.createElement(getFieldForType(currentType), {
            field,
            times: data.times,
          })}
      </OrderFormDeliverAtContent>
    </>
  )
})
