import { RM } from '@speedlo/tools'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { TOrderCategory } from '../order.types'
import { useOrderListModel } from './useOrderListModel'

export function useOrderQueryControl<TOrders>(
  categories: Maybe<TOrderCategory[]>,
) {
  return useObserver(() => {
    const orderListModel = useOrderListModel()

    const queryCategories = categories

    // TODO: Use this when order counts can be retrived from GraphQL
    // const queryCategories = categories.filter(
    //   category => !orderListModel.collapsedCategories.includes(category),
    // )

    const branchesFilter = orderListModel.branchesFilter
    const noFilter = !orderListModel.hasBranchFilter
    const skipQuery =
      noFilter || (queryCategories !== null && queryCategories.length === 0)

    // keep previous orders to smoothen transition when changing filter
    const prevOrders = React.useRef<TOrders | null>(null)

    // forget orders when skipping query
    React.useEffect(() => {
      if (skipQuery) {
        prevOrders.current = null
      }
    }, [skipQuery])

    const getOrders = RM.curry((loading: boolean, getNext: () => TOrders) => {
      if (loading && !skipQuery) {
        if (!prevOrders.current) {
          return null
        }
        return prevOrders.current
      } else {
        const orders = getNext()
        prevOrders.current = orders
        return orders
      }
    })

    const options = { queryCategories, branchesFilter, noFilter, skipQuery }

    return [getOrders, options] as [typeof getOrders, typeof options]
  })
}
