import Downshift, { ControllerStateAndHelpers, DownshiftProps } from 'downshift'
import React from 'react'

interface IProps<Item> extends Omit<DownshiftProps<Item>, 'children'> {
  children: React.ReactNodeArray
}

const context = React.createContext<ControllerStateAndHelpers<any> | undefined>(
  undefined,
)

export function useDownshift<Item>() {
  const downshift = React.useContext(context)
  if (!downshift) {
    throw new Error('DownshiftProvided missing from the tree')
  }
  return downshift as ControllerStateAndHelpers<Item>
}

export function DownshiftProvided<Item>({ children, ...rest }: IProps<Item>) {
  return (
    <Downshift {...rest}>
      {downshift => (
        <div>
          <context.Provider value={downshift}>{children}</context.Provider>
        </div>
      )}
    </Downshift>
  )
}
