import { OverlayView } from '@react-google-maps/api'
import React from 'react'

import { FOrderMapOrderFragment } from '../../../graph/generated'
import { gpsToLatLng } from '../../../helpers/gps'
import { DeliveryTimeBadge } from './DeliveryTimeBadge'

interface IProps {
  order: FOrderMapOrderFragment
  onClick(id: ID): void
  isFade?: boolean
}

export const OrderMapMarker: React.FC<IProps> = ({
  order,
  isFade,
  onClick,
}) => {
  if (!order.gps) {
    return null
  }

  const onBadgeClick = React.useCallback(() => {
    onClick(order.id)
  }, [])

  return (
    <OverlayView
      position={gpsToLatLng(order.gps)}
      mapPaneName={OverlayView.OVERLAY_MOUSE_TARGET}
    >
      <DeliveryTimeBadge
        deliverAt={order.deliverAt ? new Date(order.deliverAt) : null}
        deliveredAt={null}
        isDelayed={order.delayedDeliveryTime !== null}
        onClick={onBadgeClick}
      />
    </OverlayView>
  )
}
