import { Trans } from '@lingui/macro'
import { RM } from '@speedlo/tools'
import React from 'react'

import { useDownshift } from '../../atoms/DownshiftProvided'
import { formatAddress } from '../../helpers/formats'
import { ISuggestion } from './ISuggestion'

interface IProps {
  items: ReadonlyArray<ISuggestion>
  initialIndex?: number
}

export const AddressSuggestItems: React.FC<IProps> = ({
  items,
  initialIndex = 0,
}) => {
  const { getItemProps, highlightedIndex } = useDownshift()
  const suggests = getSuggestOptions(items)
  if (suggests.length === 0) {
    return (
      <div className="hint">
        <Trans>No match found</Trans>
      </div>
    )
  }
  return (
    <>
      {suggests.map(({ address, text }, idx) => {
        const index = idx + initialIndex
        return (
          <div
            key={addressKey(address, idx)}
            {...getItemProps({
              item: address,
              index,
              className: `item ${index === highlightedIndex ? 'active' : ''}`,
            })}
            data-testid="suggest-item"
          >
            {text}
          </div>
        )
      })}
    </>
  )
}

function getSuggestOptions(suggests: ReadonlyArray<ISuggestion>) {
  return suggests
    .filter(RM.propSatisfies(Boolean, 'gps'))
    .map(address => ({
      address,
      text: formatAddress(address),
    }))
    .filter(({ text }) => text.length > 0)
}

function addressKey(address: ISuggestion, idx: number) {
  if (address.gps) {
    return `${address.gps.latitude}@${address.gps.longitude}@${idx}`
  }
  return idx
}
