import { Trans } from '@lingui/macro'
import { CircularProgress } from '@material-ui/core'
import { Check, Warning } from '@material-ui/icons'
import { useApolloClient } from '@speedlo/graphql'
import { ApolloQueryResult } from 'apollo-client'
import { reaction } from 'mobx'
import { observer, useDisposable } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { Flex } from '../../../atoms/Flex'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { useRoot } from '../../../atoms/RootProvider'
import { AddressSuggest } from '../../../components/AddressSuggest'
import {
  QValidateLocationDocument,
  QValidateLocationQuery,
  QValidateLocationVariables,
} from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { Colors } from '../../../styling'

interface IProps {
  isVisible: boolean
}

const InputLine = styled(FlexRowSingle)`
  justify-content: flex-start;
`

const ZoneWarning = styled.div`
  display: inline-flex;
  align-items: center;
  color: ${Colors.violet};
`

const AddressValid = styled.div`
  color: ${Colors.green};
`

export const OrderFormDeliveryAddress = observer<IProps>(props => {
  const { createOrder } = useRoot()
  const [status, setStatus] = React.useState<ReactNode>(null)

  const validateAddress = useAddressValidation(createOrder.currentBranch.id)

  useDisposable(() =>
    reaction(
      () => createOrder.gps,
      async gps => {
        setStatus(<CircularProgress size={20} />)
        let isValid = false
        try {
          const validated = await validateAddress(gps)
          isValid = validated.isValid
          createOrder.setMinimalPrice(validated.minimalPrice)
        } finally {
          setStatus(null)
        }
        if (isValid) {
          setStatus(
            <AddressValid>
              <Check />
              <Trans>Address is within delivery zone</Trans>
            </AddressValid>,
          )
        } else {
          setStatus(
            <ZoneWarning>
              <Warning />
              <Trans>Address is not within any delivery zone</Trans>
            </ZoneWarning>,
          )
        }
      },
    ),
  )

  const getPreviousLocations = () => {
    if (!createOrder.vamCustomer) {
      if (createOrder.fakeAddress) {
        return [createOrder.fakeAddress]
      }
      return []
    }
    const { locations } = createOrder.vamCustomer
    return locations
  }

  return (
    <div hidden={!props.isVisible}>
      <InputLine>
        <Flex>
          <AddressSuggest
            previousLocations={getPreviousLocations()}
            // selectedAddress={
            //   createOrder.deliveryAddress || createOrder.fakeAddress || null
            // }
            onSelect={createOrder.useLocation}
            onClear={createOrder.clearLocation}
            inputPlaceholder={i18n.t`Deliver to address`.toLocaleLowerCase()}
          />
          {status}
        </Flex>
      </InputLine>
    </div>
  )
})

function useAddressValidation(branchId: ID) {
  const client = useApolloClient()
  return async ({ latitude, longitude }: GeoPoint) => {
    const variables: QValidateLocationVariables = {
      branchId,
      latitude,
      longitude,
    }
    const result: ApolloQueryResult<
      QValidateLocationQuery
    > = await client.query({
      query: QValidateLocationDocument,
      variables,
    })

    const { resolveLocation } = result.data

    return {
      isValid: resolveLocation.inDeliveryLocation,
      minimalPrice: resolveLocation.zone
        ? resolveLocation.zone.minimalOrderPrice.formattedValue
        : undefined,
    }
  }
}
