import Downshift, { DownshiftState, StateChangeOptions } from 'downshift'
import React from 'react'

import { DownshiftProvided } from '../../atoms/DownshiftProvided'
import { formatAddress } from '../../helpers/formats'
import { AddressSuggestInput } from './AddressSuggestInput'
import { AddressSuggestMenu } from './AddressSuggestMenu'
import { ISuggestion } from './ISuggestion'

interface IProps {
  onSelect(address: ISuggestion): void
  onClear(): void
  initialLocation?: ISuggestion
  previousLocations?: ReadonlyArray<ISuggestion>
  minSearchLength?: number
  inputPlaceholder?: string
}

export const AddressSuggest: React.FC<IProps> = ({
  onSelect,
  // onClear,
  previousLocations,
  minSearchLength = 3,
  inputPlaceholder,
}) => {
  const itemToString = React.useCallback(address => {
    return address ? formatAddress(address) : ''
  }, [])

  return (
    <DownshiftProvided
      stateReducer={stateReducer}
      itemToString={itemToString}
      onSelect={onSelect}
    >
      <AddressSuggestInput placeholder={inputPlaceholder} />
      <AddressSuggestMenu
        previousLocations={previousLocations}
        minSearchLength={minSearchLength}
      />
    </DownshiftProvided>
  )
}

function stateReducer(
  state: DownshiftState<any>,
  changes: StateChangeOptions<any>,
): Partial<StateChangeOptions<any>> {
  switch (changes.type) {
    case Downshift.stateChangeTypes.blurInput:
      if (state.selectedItem !== null) {
        return changes
      }
      // clear input if nothing has been selected
      return {
        ...changes,
        inputValue: '',
      }
    default:
      return changes
  }
}
